import './Service.css'
import { ResponsiveBar } from '@nivo/bar'
import {useEffect, useState} from "react";

export default function GraphBar({d}){

    let [gData,setGdata] = useState(d);
    //1차 접종자 - fir_inc, fir_inc1
    //2차 접종자 - scd_inc, scd_inc1    

    // 전국 기준일 "t_dt"
    // 전국 확진"t_hj"
    // 전국 치료중"ty_care"
    // 전국 퇴원"recover"
    // 전국 사망"death"
    // 전국 추가 확진"n_hj"
    
    // 서울시 기준일"s_dt"
    // 서울시 확진자"s_hj"
    // 서울시 치료중"s_care"
    // 서울시 퇴원"s_recover"
    // 서울시 사망"s_death"
    // 서울시 추가 확진"sn_hj"

    useEffect(()=>{
        setGdata (
            [
                {
                    "country": "확진",
                    "hot dog": d[0]['t_hj'],
                    "burger": d[0]['s_hj']
                },
                {     
                    "country": "치료중",
                    "hot dog": d[0]['ty_care'],
                    "burger": d[0]['s_care']
                },
                {     
                    "country": "퇴원",
                    "hot dog": d[0]['recover'],
                    "burger": d[0]['s_recover']
                },
                {     
                    "country": "사망",
                    "hot dog": d[0]['death'],
                    "burger": d[0]['s_death']
                },
                {     
                    "country": "추가 확진",
                    "hot dog": d[0]['n_hj'],
                    "burger": d[0]['sn_hj']
                }
            ]
        );
    },[d]);

    return(
        <>
        <div className="graph">

        <ResponsiveBar
        data={gData}
        keys={[ 'hot dog','burger']}
        indexBy="country"
        margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
        padding={0.3}
        groupMode="grouped"
        valueScale={{ type: 'linear' }}
        indexScale={{ type: 'band', round: true }}
        valueFormat={{ format: '', enabled: false }}
        colors={{ scheme: 'nivo' }}
        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: '#38bcb2',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: '#eed312',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        fill={[
            {
                match: {
                    id: 'fries'
                },
                id: 'dots'
            },
            {
                match: {
                    id: 'sandwich'
                },
                id: 'lines'
            }
        ]}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'country',
            legendPosition: 'middle',
            legendOffset: 32
        }}
        axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'food',
            legendPosition: 'middle',
            legendOffset: -40
        }}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        legends={[
            {
                dataFrom: 'keys',
                anchor: 'bottom-right',
                direction: 'column',
                justify: false,
                translateX: 120,
                translateY: 0,
                itemsSpacing: 2,
                itemWidth: 100,
                itemHeight: 20,
                itemDirection: 'left-to-right',
                itemOpacity: 0.85,
                symbolSize: 20,
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemOpacity: 1
                        }
                    }
                ]
            }
        ]}
    />
            </div>
        </>
    )
}