import Icon1 from './img/webicon-behance.png'
import Icon2 from './img/webicon-dribbble.png'
import Icon3 from './img/webicon-facebook.png'
import Icon4 from './img/webicon-flickr.png'
import Icon5 from './img/webicon-googleplus.png'
import Icon6 from './img/webicon-pinterest.png'
import Icon7 from './img/webicon-twitter.png'

export default function IconImg(){

    let imgArr=[ Icon1, Icon2, Icon3, Icon4, Icon5, Icon6, Icon7]

    return(
        <>
        { 
            imgArr.map(function(src,k){
            return <img src={src} alt= "img" key={k}/>
            })
        }
        </>
    )
}