import './Graph.css'
import Corona from './corona.json'
import {useState} from "react";
import GraphBar from './GraphBar'

export default function Graph(){

    let [firData,setData] = useState(Corona.DATA);
    
    function dataChange(e){
        setData(
            Corona.DATA.filter(day => (
                day['t_dt'] === e.target.value
            ))
        )
    }
    function allBtn(e){
        setData(Corona.DATA);
    }
    return(
        <>
        <div>
            <h2>코로나 현황</h2>
            <button onClick={allBtn}>전체보기</button>
            <form>   
                <select id="vr_day" onChange={dataChange}>
                    {
                        Corona.DATA.map(function(data,k){
                            return (
                                <option key={k}>
                                    {data['t_dt']}
                                </option>
                            )
                        })
                    }
                </select>
            </form>
            <table className="corona_tb" border="1">
                <thead>
                    <tr>
                        <th>전국 기준일</th>
                        <th>전국 확진</th>
                        <th>전국 추가 확진</th>
                        <th>전국 치료중</th>
                        <th>전국 퇴원</th>
                        <th>전국 사망</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        firData.map(function(dataView,k){
                            return (
                                <tr key={k}>   
                                    <td>{dataView['t_dt']}</td>
                                    <td>{dataView['t_hj']}</td>
                                    <td>{dataView['n_hj']}</td>
                                    <td>{dataView['ty_care']}</td>
                                    <td>{dataView['recover']}</td>
                                    <td>{dataView['death']}</td>
                                </tr>
                            );
                        })
                    } 
                </tbody>
            </table>
            <table className="corona_tb2">
                <thead>
                    <tr>
                        <th>서울시 기준일</th>
                        <th>서울시 확진자</th>
                        <th>서울시 추가 확진</th>

                        <th>서울시 치료중</th>
                        <th>서울시 퇴원</th>

                        <th>서울시 사망</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        firData.map(function(dataView,k){
                            return (
                                <tr key={k}>   
                                    <td>{dataView['s_dt']}</td>
                                    <td>{dataView['s_hj']}</td>
                                    <td>{dataView['sn_hj']}</td>
                                    <td>{dataView['s_care']}</td>
                                    <td>{dataView['s_recover']}</td>
                                    <td>{dataView['s_death']}</td>
                                </tr>
                            );
                        })
                    } 
                </tbody>
            </table>
        </div>
        <GraphBar d={firData}/>
        </>
    )
}