import Img01 from './img/img1.png'
import Img02 from './img/img2.png'
import Img03 from './img/img3.png'
import Img04 from './img/img4.png'
import Img05 from './img/img5.png'
import Img06 from './img/img6.png'
import Img07 from './img/img7.png'
import Img08 from './img/img8.png'
import Img09 from './img/img9.png'
import Img10 from './img/img10.png'
import Img11 from './img/img11.png'
import Img12 from './img/img12.png'

import hoverImg1 from './img/icon2.png'
import hoverImg2 from './img/icon5.png'

import { Link } from 'react-router-dom'

export default function Homeimg(){
    let homeImgArr = [Img01,Img02,Img03,Img04,Img05,Img06,Img07,Img08,Img09,Img10,Img11,Img12]

    return(
        <>
        {
            homeImgArr.map(function(src,k){
                return <li key={k}>
                    <b>Cool App Design
                        <small>development, mobile</small>
                        <Link to="/img01">
                            <img src={hoverImg1} alt=""/>
                            <img src={hoverImg2} alt=""/>
                        </Link>
                    </b>
                    <img src={src} alt=""/>
                    </li>
            })
        }
        </>
    )
}